<template>
    <div class="role">
        <el-row :gutter="20" class="functionalArea">
            <el-col :span="6">
                <el-button-group>
                    <el-button type="primary" icon="el-icon-plus" @click="addBtnFn"></el-button>
                    <el-button type="primary" icon="el-icon-share"></el-button>
                    <el-button type="primary" icon="el-icon-delete" @click="clearBtnFn"></el-button>
                </el-button-group>
            </el-col>
            <el-col :span="6" :offset="12">
                <el-input placeholder="请输入内容" v-model="searchInput" clearable>
                    <template slot="append">
                        <el-button type="success" plain>搜索</el-button>
                    </template>
                </el-input>
            </el-col>
        </el-row>

        <el-table
                ref="multipleTable"
                :data="tableData"
                style="width: 100%"
                :header-cell-style="headerCellStyle"
                :cell-style="cellStyle"
                @select="handleSelectionChange"
                @select-all="selectAllFn"
        >
            <el-table-column
                    type="selection"
                    width="55">
            </el-table-column>
            <el-table-column
                    label="序号"
                    width="60">
                <template slot-scope="scope">
                    <span style="margin-left: 10px">{{ scope.row.index }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="地址"
                    width="240">
                <template slot-scope="scope">
                    <span style="margin-left: 10px">{{ scope.row.name }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="代号"
                    width="240">
                <template slot-scope="scope">
                    <span style="margin-left: 10px">{{ scope.row.seq }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="操作"
                    min-width="240">
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-dialog title="添加地址" :visible.sync="addFormVisible" width="800px">
            <el-form :model="form">
                <el-row :gutter="20">
                    <el-col :span="24">
                        <el-form-item label="地址" :label-width="formLabelWidth">
                            <el-input v-model="form.name" placeholder="请输入地址" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="代号" :label-width="formLabelWidth">
                            <el-input v-model="form.seq" placeholder="请输入代号" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="addSureFn">确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="编辑地址" :visible.sync="fixFormVisible" width="800px">
            <el-form :model="form1">
                <el-row :gutter="20">
                    <el-col :span="24">
                        <el-form-item label="地址" :label-width="formLabelWidth">
                            <el-input v-model="form1.name" placeholder="请输入地址" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="24">
                        <el-form-item label="代号" :label-width="formLabelWidth">
                            <el-input v-model="form1.seq" placeholder="请输入代号" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="fixFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="fixSureFn">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>

export default {
  name: 'ATheme',
  data () {
    return {
      searchInput: '',
      tableData: [],
      jurisdictionData: [],

      addFormVisible: false,
      formLabelWidth: '120px',
      form: {
        name: '',
        seq: ''
      },
      fixFormVisible: false,
      form1: {
        name: '',
        seq: '',
        id: ''
      },

      selectIds: [],

      notifyPromise: Promise.resolve()
    }
  },
  watch: {
  },
  created: function () {
    this.getAddressFn()
  },
  methods: {
    addBtnFn () {
      this.addFormVisible = true
    },
    addSureFn () {
      let temp = {
        name: this.form.name.replace(/(^\s*)|(\s*$)/g, ''),
        seq: this.form.seq.replace(/(^\s*)|(\s*$)/g, ''),
        user_id: sessionStorage.user_id
      }
      if (temp.name !== '' && temp.seq !== '') {
        this.addFn(temp)
      } else {
        if (temp.seq === '') {
          this.notifyFn('提示', '代号输入有误')
        }
        if (temp.name === '') {
          this.notifyFn('提示', '地址输入有误')
        }
      }
    },
    // 通知，解决element-ui，同时调用notify时，通知重叠的问题
    notifyFn (title, msg, type = 'warning') {
      this.notifyPromise = this.notifyPromise.then(this.$nextTick).then(() => {
        this.$notify({
          title: title,
          message: msg,
          type: type,
          dangerouslyUseHTMLString: true,
          duration: 5000,
          position: 'top-right'
        })
      })
    },
    handleEdit (index, row) {
      console.log(index, row)
      this.fixFormVisible = true
      this.form1.name = row.name
      this.form1.seq = row.seq
      this.form1.id = row.id
    },
    fixSureFn () {
      let temp = {
        id: this.form1.id,
        name: this.form1.name.replace(/(^\s*)|(\s*$)/g, ''),
        seq: this.form1.seq.replace(/(^\s*)|(\s*$)/g, ''),
        user_id: sessionStorage.user_id
      }
      if (temp.name !== '' && temp.seq !== '') {
        this.fixFn(temp)
      } else {
        if (temp.seq === '') {
          this.notifyFn('提示', '代号输入有误')
        }
        if (temp.name === '') {
          this.notifyFn('提示', '地址输入有误')
        }
      }
    },
    clearBtnFn () {
      if (this.selectIds.length) {
        this.$confirm('确定删除所选地址', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let params = {
            id_list: this.selectIds,
            user_id: sessionStorage.user_id
          }
          this.deleteSingleFn(params)
        }).catch(() => {
          this.notifyFn('提示', '已取消删除')
        })
      } else {
        this.notifyFn('提示', '未选择地址')
      }
    },
    handleDelete (index, row) {
      console.log(row)
      this.$confirm('确定删除地址：' + row.name, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let params = {
          id_list: [row.id],
          user_id: sessionStorage.user_id
        }
        this.deleteSingleFn(params)
      }).catch(() => {
        this.notifyFn('提示', '已取消删除')
      })
    },
    async getAddressFn () {
      let params = {}
      try {
        let res = await this.$api.unsplash.getAddress(params)
        if (res.errno === '0') {
          this.tableData = this.addSequence(res.data.addrs)
        } else {
          this.notifyFn('提示', '获取地址失败')
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async addFn (params) {
      try {
        let res = await this.$api.unsplash.postAddAddress(params)
        if (res.errno === '0') {
          this.addFormVisible = false
          this.notifyFn('提示', '增加地址成功', 'success')
          this.getAddressFn()
        } else {
          this.notifyFn('提示', '增加地址失败')
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async fixFn (params) {
      try {
        let res = await this.$api.unsplash.postfixAddress(params)
        if (res.errno === '0') {
          this.fixFormVisible = false
          this.notifyFn('提示', '修改地址成功', 'success')
          this.getAddressFn()
        } else {
          this.notifyFn('提示', '修改地址失败')
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async deleteSingleFn (params) {
      try {
        let res = await this.$api.unsplash.postClearAddress(params)
        if (res.errno === '0') {
          this.notifyFn('提示', '删除成功', 'success')
          this.selectIds = []
          this.getAddressFn()
        } else {
          this.notifyFn('提示', '删除失败')
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    handleSelectionChange (selection, row) { // 单击
      console.log(selection)
      this.selectIds = []
      for (let i = 0; i < selection.length; i++) {
        this.selectIds.push(selection[i].id)
      }
    },
    selectAllFn (selection) { // 全选
      console.log(selection)
      this.selectIds = []
      for (let i = 0; i < selection.length; i++) {
        this.selectIds.push(selection[i].id)
      }
    },
    headerCellStyle ({ row, column, rowIndex, columnIndex }) {
      if (rowIndex > -1) {
        return {
          'text-align': 'center'
        }
      }
    },
    cellStyle ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex >= 0) {
        return {
          'text-align': 'center'
        }
      }
    },
    myAlert (txt, title, self) {
      self.$alert(txt, title, {
        confirmButtonText: '确定',
        callback: action => {
        }
      })
    },
    addSequence (temp) {
      for (let i = 0; i < temp.length; i++) {
        temp[i].index = i + 1
      }
      return temp
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
    .role{
        .functionalArea{
            height: 80px;
        }
        .headerCellStyle{
            text-align: center;
        }
        .el-dialog__body {
            padding: 30px 60px 30px 20px;
        }
    }
</style>



